import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'
import ButtonRaised from '../../components/Site/ButtonRaised.js'
import ButtonFlat from '../../components/Site/ButtonFlat.js'


const Buttons = () => (
        <main id='Buttons' className="ph3 ph5-ns pt3 mb5">
        <SectionTitle Number='08' Title='Buttons'/>
        

        <h2 className='mt5 mb5'>Primary action</h2>
		<ButtonRaised Action='Primary action'/>
		<ButtonRaised Action='Primary disabled' Color='bg-disabled'/>


		<h2 className='mt5 mb5'>Secondary actions</h2>
		<ButtonFlat Action='Secondary action' TextColor='link-active' />
		<ButtonFlat Action='Primary Disabled' TextColor='disabled' />
		<ButtonFlat Action='Destructive Action' TextColor='alert' />

      </main>

)



export default Buttons



