import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'


const List = () => (
        <main id='Principles' className="ph3 ph5-ns pt3">
        <SectionTitle Number='11' Title='hmmm idk'/>
     
      <form className="pa4">
        <fieldset id="favorite_movies" className="bn">
          <legend className="fw7 mb2">Favorite Movies</legend>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="spacejam" defaultValue="spacejam" />
            <label htmlFor="spacejam" className="lh-copy">Space Jam</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="airbud" defaultValue="airbud" />
            <label htmlFor="airbud" className="lh-copy">Air Bud</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="hocuspocus" defaultValue="hocuspocus" />
            <label htmlFor="hocuspocus" className="lh-copy">Hocus Pocus</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="diehard" defaultValue="diehard" />
            <label htmlFor="diehard" className="lh-copy">Die Hard</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="primer" defaultValue="primer" />
            <label htmlFor="primer" className="lh-copy">Primer</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="proxy" defaultValue="proxy" />
            <label htmlFor="proxy" className="lh-copy">Hudsucker Proxy</label>
          </div>
          <div className="flex items-center mb2">
            <input className="mr2" type="checkbox" id="homealone" defaultValue="homealone" />
            <label htmlFor="homealone" className="lh-copy">Home Alone</label>
          </div>
        </fieldset>
      </form>



      </main>

)



export default List



