import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'
import ListItem from '../../components/Site/ListItem.js'


const Principles = () => (
        <main id='Principles' className="ph3 ph5-ns pt3">
        <SectionTitle Number='01' Title='Principles'/>
		<ul className="list pl0 ma0 pa0">
       	<ListItem Title='Unified' Description='Each piece is part of a greater whole and should contribute positively to the system at scale. There should be no isolated features or outliers.'/>
		<ListItem Title='Universal' Description='Airbnb is used around the world by a wide global community. Our products and visual language should be welcoming and accessible.'/>
       	<ListItem Title='Iconic' Description='We’re focused when it comes to both design and functionality. Our work should speak boldly and clearly to this focus.'/>
       	<ListItem Title='Conversational' Description='Our use of motion breathes life into our products, and allows us to communicate with users in easily understood ways.'/>
		</ul>
      </main>

)



export default Principles



